.skills {
    text-align: center;
    padding: 60px 0;
    color: #FAFAFA;
    font-family: 'Instrument Sans', sans-serif;
  }
  
  h2 {
    font-size: 3rem;
    text-transform: uppercase;
    margin-bottom: 50px;
  }
  
  .skills-container {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 60px;
  }
  
  .skill-category {
    background-color: #1a1a1a;
    padding: 20px;
    border-radius: 16px;
    max-width: 300px;
    text-align: center;
    transition: transform 0.3s ease;
  }
  
  .skill-category:hover {
    transform: scale(1.05);
  }
  
  .skill-category h4 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: #D3E87D;
  }
  
  .skills-logos {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }
  
  .skill-item img {
    width: 50px;
    height: 50px;
  }
  
  .skill-item p {
    margin: 0;
    font-size: 1rem;
    color: #b3b3b3;
  }
  
  @media (max-width: 1024px) {
    .skills-container {
      gap: 40px;
    }
  
    .skill-category {
      max-width: 250px;
    }
  }
  
  @media (max-width: 768px) {
    h2 {
      font-size: 2rem;
    }
  
    .skills-container {
      flex-direction: column;
      gap: 30px;
    }
  
    .skill-category {
      max-width: 100%;
    }
  }
  