.about-section {
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: start;
    gap: 10%;
    padding: 80px 50px;
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
}

@media(max-width:768px) {
    .about-section {
        grid-template-columns: 1fr;
        gap: 20px;
        padding: 40px 20px;
    }
}

@media(max-width:1024px) {
    .about-section {
        grid-template-columns: 1fr;
        gap: 20px;
        padding: 40px 20px;
    }
}

.yellow {
    color: #D3E87D;
}

.journey-number {
    font-size: 36px;
    font-weight: bold;
}

.journey-section {
    margin-top: 50px;
}

@media(max-width:768px) {
    .journey-section {
        margin-top: 30px;
    }
}

.journey-item {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 30px;
}

@media(max-width:768px) {
    .journey-item {
        flex-direction: column;
        gap: 10px;
    }
}

.journey-title {
    font-size: 30px;
}

@media(max-width:768px) {
    .journey-title {
        font-size: 16px;
        display: flex;
        justify-content: center;
    }
}

@media(max-width:1024px) {
    .journey-title {
        font-size: 16px;
        display: flex;
        justify-content: center;
    }
}

.about-label {
    font-family: 'Instrument Sans', sans-serif;
    font-weight: 600;
    font-size: 40px;
    color: #D3E87D;
    display: flex;
    justify-content: center;
}

.about-description {
    font-family: 'Instrument Sans', sans-serif;
    font-weight: 400;
    font-size: 30px;
    line-height: 1.4;
    color: #FFFFFF;
}

@media(max-width:768px) {
    .about-description {
        font-size: 14px;
    }
}

h3 {
    @media(max-width: 768px) {
        justify-content:center;
        margin: 0;
    }
}

p {
    @media(max-width: 768px) {
        justify-content:center;
    }
}