.projects {
    text-align: center;
    padding: 60px 0;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
  }
  
  h2 {
    font-family: 'Instrument Sans', sans-serif;
    font-weight: 700;
    font-size: 60px;
    color: #FAFAFA;
    margin-bottom: 50px;
  }
  
  .project-gallery {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
    padding: 20px;
  }
  
  @media (max-width: 1024px) {
    .project-gallery {
      grid-template-columns: repeat(2, 1fr);
      gap: 30px;
    }
  }
  
  @media (max-width: 768px) {
    .project-gallery {
      grid-template-columns: 1fr;
      gap: 20px;
    }
  }
  
  .project {
    background-color: #000000;
    border-radius: 16px;
    overflow: hidden;
    cursor: pointer;
    transition: transform 0.3s ease;
  }
  
  .project:hover {
    transform: scale(1.05);
  }
  
  .project .project-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  
  .project .project-info {
    padding: 20px;
    background: linear-gradient(to bottom, #0d0d0d, #191B1B);
    text-align: center;
    font-family: 'Instrument Sans', sans-serif;
    color: #FAFAFA;
  }
  
  .project .project-info .project-link {
    font-size: 20px;
    font-weight: bold;
    color: #FAFAFA;
    text-decoration: none;
    background: none;
    border: none;
    padding: 0;
    cursor: pointer;
    outline: none;
    display: inline-block;
    transition: color 0.3s ease;
  }
  
  .project .project-info .project-link:hover {
    color: #D3E87D;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .modal-content {
    background: #000;
    padding: 40px;
    border-radius: 16px;
    max-width: 600px;
    width: 90%;
    text-align: left;
    color: #FAFAFA;
  }
  
  .close-button {
    position: absolute;
    top: 10px;
    right: 20px;
    cursor: pointer;
    font-size: 24px;
  }
  
  .github-link {
    color: #D3E87D;
    text-decoration: none;
  }
  
  .github-link:hover {
    color: #fff;
  }
  